:root {
  --primary-text: #000;
  --secundary-text: #4d4d4d;
  --p-text: #9e9e9e;
}

.wrapper {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.title {
  font-size: 36px;
  font-weight: bold;
  margin-top: 50px;
  color: var(--secundary-text);
}

.subTitle {
  max-width: 194px;
  font-size: 18px;
  text-align: center;
  color: var(--p-text);
  margin-top: 32px;
  margin-bottom: 70px;
}

.subTitle > p {
  margin-bottom: 0px !important;
}

.credits {
  max-width: 194px;
  font-size: 8px;
  text-align: center;
  color: rgb(223, 223, 223);
  margin-top: 10px;
}

.start_button {
  width: 200px;
  height: 50px;
  background-color: #d9534f !important;
  border-radius: 25px !important;
  border: none;
  margin-top: 75px;
  color: #fff !important;
}

.button {
  width: 100px;
  height: 50px;
  background-color: #d9534f !important;
  border-radius: 15px !important;
  border: none;
  color: #fff !important;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 25px;
}

.start_button > a {
  color: #fff !important;
  width: 200px;
  height: 50px;
}

.author {
  color: var(--p-text);
  text-align: center;
}

.author_container {
  margin-top: 30px;
}
